<template>
  <div>
    <!-- Navbar with "Back" button and centered title -->
    <div class="navbar">
      <button class="btn btn-back" @click="goBack">Back</button>
      <h2 class="title">Add New Character</h2>
      <div class="navbar-spacer"></div> <!-- This ensures proper centering of the title -->
    </div>

    <!-- Form Container -->
    <div class="form-container">
      <h2>Add New Character</h2>
      <div class="form-group">
        <label for="name">Name:</label>
        <input id="name" v-model="character.name" placeholder="Enter character name" />
      </div>

      <div class="form-group">
        <label for="path">Path:</label>
        <input id="path" v-model="character.path" placeholder="Enter image URL" />
      </div>

      <div class="form-group">
        <label for="htmlCode">HTML Code:</label>
        <textarea id="htmlCode" v-model="character.htmlCode" placeholder="Enter HTML code"></textarea>
      </div>

      <div class="form-group">
        <label for="tags">Tags:</label>
        <input
          id="tags"
          v-model="tagsInput"
          placeholder="Enter tags, separated by commas"
        />
      </div>

      <!-- Action Buttons -->
      <div class="action-buttons">
        <button class="btn btn-cancel" @click="cancelAdd">Cancel</button>
        <button class="btn btn-save" @click="addCharacter">Add</button>
      </div>

      <!-- Added Overlay -->
      <AppOverlay :visible="added" message="Added!" />
    </div>
  </div>
</template>

<script>
import AppOverlay from './AppOverlay.vue';

export default {
  components: {
    AppOverlay,
  },
  data() {
    return {
      character: {
        name: '',
        path: '',
        htmlCode: '',
        tags: [], // Ensure 'tags' is initialized as an empty array
      },
      tagsInput: '', // Used for the input field
      added: false,  // Controls the overlay visibility
    };
  },
  methods: {
    addCharacter() {
      if (!this.character.name || !this.character.path) {
        alert('Name and Path are required.');
        return;
      }

      this.character.tags = this.tagsInput.split(',').map(tag => tag.trim());

      // Simulating a successful character addition
      this.added = true;
      setTimeout(() => {
        this.added = false;
        this.$router.push({ name: 'CharacterList' });
      }, 2000);
    },
    copyHTMLCode() {
      navigator.clipboard.writeText(this.character.htmlCode)
        .then(() => alert('HTML Code copied!'))
        .catch(err => console.error('Error copying HTML Code:', err));
    },
    cancelAdd() {
      this.$router.push({ name: 'CharacterList' });
    },
    goBack() {
      this.$router.push({ name: 'CharacterList' });
    },
  }
};
</script>

